import React, { useMemo } from 'react';
import cx from 'classnames';
import dayjs from 'dayjs';
import { DatePicker, Select } from '@douyinfe/semi-ui';
import { allRoutes, getDateOptions, getRegionOptions } from '../../constants/route';
import './index.scss';

const regionOptions = getRegionOptions(allRoutes);
const dateOptions = getDateOptions(allRoutes);

type Props = {
  className?: string;
  value?: FilterForm;
  onChange?: (value: FilterForm) => void;
};

const Filter: React.FC<Props> = (props) => {
  const prefix = 'com-filter';
  const { className, value, onChange } = props;
  const range = useMemo(() => {
    return (value?.range || []).map(item => dayjs(item).toDate());
  }, [value?.range]);

  const getDisabledDate = (date?: Date) => {
    const key = date?.valueOf() || 0;
    return !dateOptions[key];
  };

  const handleChangeRegion = (region: any) => {
    if (!value || !onChange) {
      return false;
    }
    const newValue = { ...value };
    newValue.region = region;
    onChange(newValue);
  };

  const handleChangeRange = (range: any) => {
    if (!value || !onChange) {
      return false;
    }
    const newValue = { ...value };
    newValue.range = range.map((item: any) => item.valueOf());
    onChange(newValue);
  };

  return (
    <div className={cx(prefix, className)}>
      <Select
        className={`${prefix}-region`}
        placeholder=''
        showClear={true}
        optionList={regionOptions}
        value={value?.region}
        onChange={handleChangeRegion}
      />
      <DatePicker
        className={`${prefix}-range`}
        type='dateRange'
        format='yyyy.MM.dd ~ yyyy.MM.dd'
        showClear={true}
        disabledDate={getDisabledDate}
        value={range}
        onChange={handleChangeRange}
      />
    </div>
  );
};

export default Filter;
