import React from 'react';
import cx from 'classnames';
import dayjs from 'dayjs';
import './index.scss';
import { allImages } from '../../constants/route';

type Props = {
  className?: string;
  route?: RouteItem;
};

const RouteItem: React.FC<Props> = (props) => {
  const prefix = 'com-route-item';
  const { className, route } = props;

  const handleClick = () => {
    window.open('https://' + route?.video, '_blank');
  };

  return (
    <div
      className={cx(prefix, className)}
      id={`date-${route?.date}`}
      onClick={handleClick}
    >
      <div className={`${prefix}-container`}>
        <div className={`${prefix}-left`}>
          <img
            className={`${prefix}-left-image`}
            src={allImages[route?.image || '']}
            alt={route?.title}
          />
        </div>
        <div className={`${prefix}-right`}>
          <div className={`${prefix}-right-title`}>{route?.title}</div>
          <div className={`${prefix}-right-desc`}>
            <div className='desc-item'>{route?.start.name}</div>
          </div>
          <div className={`${prefix}-right-desc`}>
            <div className='desc-item'>{route?.start.name}</div>
          </div>
          <div className={`${prefix}-right-desc`}>
            <div className='desc-item'>{dayjs(route?.date).format('YYYY.MM.DD')}</div>
            <div className='desc-item'>{((route?.distance || 0) / 1000).toFixed(1)}</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RouteItem;
