import route20230508 from '../data/20230508.json';
import route20230511 from '../data/20230511.json';
import route20230512 from '../data/20230512.json';
import route20230515 from '../data/20230515.json';
import route20230517 from '../data/20230517.json';
import image20230508 from '../assets/image/20230508.webp';
import image20230511 from '../assets/image/20230511.webp';
import image20230512 from '../assets/image/20230512.webp';
import image20230515 from '../assets/image/20230515.webp';
import image20230517 from '../assets/image/20230517.webp';

export const allRoutes: RouteItem[] = [
  route20230508,
  route20230511,
  route20230512,
  route20230515,
  route20230517,
].reverse();

export const allImages: { [date: string]: string } = {
  20230508: image20230508,
  20230511: image20230511,
  20230512: image20230512,
  20230515: image20230515,
  20230517: image20230517,
};

export const getRegionOptions = (list: RouteItem[]) => {
  const regionSet = new Set<string>();
  for (const item of list) {
    for (const name of item.region) {
      regionSet.add(name);
    }
  }
  return Array.from(regionSet).map(item => ({
    label: item,
    value: item,
  }));
};

export const getDateOptions = (list: RouteItem[]) => {
  return list
    .map(item => item.date)
    .reduce((result: { [date: string]: boolean }, date) => {
      result[date] = true;
      return result;
    }, {});
};

export const getFilteredRoute = (list: RouteItem[], filter: FilterForm) => {
  const { region, range: [start, end] } = filter;
  return list.filter(item => {
    const checkRegion = !region || item.region.includes(region);
    const checkRange = (!start && !end) || (item.date >= start && item.date <= end);
    return checkRegion && checkRange;
  });
};

export const getLineLayerData = (list: RouteItem[]) => {
  const features = list.map(item => ({
    type: 'Feature',
    properties: {
      name: String(item.date),
    },
    geometry: {
      type: 'LineString',
      coordinates: item.route,
    },
  }));
  return {
    type: 'FeatureCollection',
    features,
  };
};
