// @ts-nocheck
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import cx from 'classnames';
import dayjs from 'dayjs';
import _ from 'lodash';
import { getLineLayerData } from '../../constants/route';
import './index.scss';

type Props = {
  className?: string;
  routes?: RouteItem[];
  onClick?: (item: RouteItem) => void;
};

const MainMap: React.ForwardRefRenderFunction<unknown, Props> = (props) => {
  const prefix = 'main-map';
  const containerId = 'mapContainer';
  const { className, routes, onClick } = props;
  const [labelSize, setLabelSize] = useState('large');
  const centerPoint = useRef(new BMapGL.Point(88, 39.5));

  const [mapInstance, setMapInstance] = useState(null);
  const lineInstance = useRef(null);
  const lineData = useMemo(() => getLineLayerData(routes || []), [routes]);
  const pointData = useMemo(() => {
    return (routes || []).map(item => {
      const labelContent = labelSize === 'small'
        ? `<div class='map-icon-label'></div>`
        : `
          <div class='map-date-label'>
            <span>${dayjs(item.date).format('YY.MM.DD')}</span>
            <div class='map-date-label-icon'></div>
          </div>
        `;
      const offset = labelSize === 'small'
        ? new BMapGL.Size(-3, -3) :
        new BMapGL.Size(-42, -34);
      const position = new BMapGL.Point(...item.start.coordinate);
      const label = new BMapGL.Label(labelContent, { offset, position });
      label.setStyle({ border: 'none', padding: 0, backgroundColor: 'transparent' });
      label.addEventListener('click', () => onClick && onClick(item));
      return label;
    });
  }, [labelSize, routes, onClick]);

  const initMapInstance = useCallback(() => {
    if (mapInstance) {
      return false;
    }
    const instance = new BMapGL.Map(containerId);
    instance.enableScrollWheelZoom(true);
    instance.addEventListener('zoomend', (e) => {
      const zoomLevel = _.get(e, 'target.zoomLevel') || 10;
      if (zoomLevel >= 7.2) {
        setLabelSize('large');
      } else {
        setLabelSize('small');
      }
    });
    setMapInstance(instance);
  }, [mapInstance, setLabelSize]);

  const initLineInstance = useCallback(() => {
    if (!mapInstance) {
      return false;
    }
    lineInstance.current = new BMapGL.LineLayer({
      enablePicked: true,
      autoSelect: true,
      pickWidth: 30,
      pickHeight: 30,
      selectedColor: '#fa5741',
      style: {
        strokeWeight: 3,
        strokeColor: '#fa5741',
      },
    });
    lineInstance.current.addEventListener('click', () => { });
  }, [mapInstance]);

  useEffect(() => {
    initMapInstance();
  }, [initMapInstance]);

  useEffect(() => {
    if (mapInstance) {
      if (routes?.length) {
        const coord = routes[0].start.coordinate;
        centerPoint.current = new BMapGL.Point(coord[0] - 0.5, coord[1]);
      }
      mapInstance.centerAndZoom(centerPoint.current, 9);
    }
  }, [mapInstance, routes]);

  useEffect(() => {
    if (lineInstance.current) {
      mapInstance.removeNormalLayer(lineInstance.current);
    }
    initLineInstance();
    if (lineInstance.current) {
      lineInstance.current.setData(lineData);
      mapInstance.addNormalLayer(lineInstance.current);
    }
  }, [initLineInstance, lineData]);

  useEffect(() => {
    if (mapInstance) {
      pointData.forEach(point => mapInstance.addOverlay(point));
      return () => {
        pointData.forEach(point => mapInstance.removeOverlay(point));
      };
    }
  }, [mapInstance, pointData]);

  return (
    <div className={cx(prefix, className)} id={containerId}></div>
  );
};

export default React.forwardRef(MainMap);
