import React, { useEffect, useMemo, useState } from 'react';
import { MainMap, Filter, RouteList } from '../../components';
import './index.scss';
import { allRoutes, getFilteredRoute } from '../../constants/route';

const Home: React.FC = () => {
  const prefix = 'home';
  const [filterForm, setFilterForm] = useState<FilterForm>(() => {
    const params = new URLSearchParams(window.location.search.slice(1));
    const region = params.get('region');
    const start = params.get('start');
    const end = params.get('end');
    return {
      region: region || '',
      range: start && end ? [parseInt(start, 10), parseInt(end, 10)] : [],
    };
  });
  const filteredList = useMemo(() => {
    return getFilteredRoute(allRoutes, filterForm);
  }, [filterForm]);

  const handleClickMapIcon = (item: RouteItem) => {
    const element = document.getElementById(`date-${item.date}`);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
      element.classList.add('hover-status');
      setTimeout(() => element.classList.remove('hover-status'), 3000);
    }
  };

  useEffect(() => {
    const params = new URLSearchParams();
    params.append('region',filterForm.region);
    params.append('start', String(filterForm.range[0] || ''));
    params.append('end', String(filterForm.range[1] || ''));
    window.history.pushState({}, '', `?${params.toString()}`);
  }, [filterForm]);

  return (
    <div className={`${prefix}`}>
      <Filter
        className={`${prefix}-filter`}
        value={filterForm}
        onChange={setFilterForm}
      />
      <RouteList
        className={`${prefix}-list`}
        routes={filteredList}
      />
      <MainMap
        className={`${prefix}-map`}
        routes={filteredList}
        onClick={handleClickMapIcon}
      />
    </div>
  );
};

export default Home;
